// FILE: src/pages/SettingsPage.tsx
/// ANCHOR: SettingsPage
import { Link } from 'react-router-dom';
import AppLayout from '../components/layout/AppLayout';
import SettingsPanel from '../components/dashboard/SettingsPanel';
import RolePermissionsPanel from '../components/dashboard/RolePermissionsPanel';
import GodownPanel from '../components/dashboard/GodownPanel';
import BackupPanel from '../components/dashboard/BackupPanel';

const SettingsPage = () => (
  <AppLayout>
    <div className="page-container">
      <div className="page-header">
        <h2>Settings</h2>
        <p>Configure application and manage data</p>
      </div>
      
      {/* Quick Access Links */}
      <div style={{ 
        display: 'flex', 
        gap: 'var(--spacing-lg)', 
        marginBottom: 'var(--spacing-2xl)',
        flexWrap: 'wrap'
      }}>
        <Link 
          to="/settings/company-info" 
          style={{
            display: 'inline-flex',
            alignItems: 'center',
            gap: 'var(--spacing-md)',
            padding: 'var(--spacing-md) var(--spacing-xl)',
            background: 'var(--color-primary)',
            color: 'var(--color-text-white)',
            textDecoration: 'none',
            borderRadius: 'var(--radius-lg)',
            fontWeight: 500,
            fontSize: '0.9375rem',
            transition: 'all 0.2s',
            boxShadow: 'var(--shadow-sm)'
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.background = 'var(--color-primary-dark)';
            e.currentTarget.style.transform = 'translateY(-2px)';
            e.currentTarget.style.boxShadow = 'var(--shadow-md)';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.background = 'var(--color-primary)';
            e.currentTarget.style.transform = 'translateY(0)';
            e.currentTarget.style.boxShadow = 'var(--shadow-sm)';
          }}
        >
          <span></span>
          <span>Company Information</span>
        </Link>
        <Link 
          to="/settings/billing-templates" 
          style={{
            display: 'inline-flex',
            alignItems: 'center',
            gap: 'var(--spacing-md)',
            padding: 'var(--spacing-md) var(--spacing-xl)',
            background: 'var(--color-secondary)',
            color: 'var(--color-text-white)',
            textDecoration: 'none',
            borderRadius: 'var(--radius-lg)',
            fontWeight: 500,
            fontSize: '0.9375rem',
            transition: 'all 0.2s',
            boxShadow: 'var(--shadow-sm)'
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.background = 'var(--color-secondary-dark)';
            e.currentTarget.style.transform = 'translateY(-2px)';
            e.currentTarget.style.boxShadow = 'var(--shadow-md)';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.background = 'var(--color-secondary)';
            e.currentTarget.style.transform = 'translateY(0)';
            e.currentTarget.style.boxShadow = 'var(--shadow-sm)';
          }}
        >
          <span></span>
          <span>Billing Templates</span>
        </Link>
      </div>

      <div className="dashboard-grid">
        <SettingsPanel />
        <RolePermissionsPanel />
        <GodownPanel />
        <BackupPanel />
      </div>
    </div>
  </AppLayout>
);

export default SettingsPage;

