// FILE: src/stores/stockStore.ts
/// ANCHOR: StockStore
import { create } from 'zustand';
import type { StockSnapshotEntry } from '@shared/ipc';
import { invoke } from '../api/ipcClient';

interface StockState {
  entries: StockSnapshotEntry[];
  status: 'idle' | 'loading' | 'error';
  error?: string;
  load: () => Promise<void>;
}

export const useStockStore = create<StockState>((set) => ({
  entries: [],
  status: 'idle',
  error: undefined,
  load: async () => {
    set({ status: 'loading', error: undefined });
    try {
      const entries = await invoke('ipc.stock.snapshot', undefined);
      set({ entries, status: 'idle' });
    } catch (error) {
      set({
        status: 'error',
        error: error instanceof Error ? error.message : 'Failed to load stock'
      });
    }
  }
}));

