// FILE: src/utils/numberToWords.ts
/// ANCHOR: NumberToWords
/**
 * Converts a number to words in Indian numbering system
 * Example: 3108.00 -> "THREE THOUSAND ONE HUNDRED EIGHT ONLY"
 */
export const numberToWords = (num: number): string => {
  const ones = [
    '', 'ONE', 'TWO', 'THREE', 'FOUR', 'FIVE', 'SIX', 'SEVEN', 'EIGHT', 'NINE',
    'TEN', 'ELEVEN', 'TWELVE', 'THIRTEEN', 'FOURTEEN', 'FIFTEEN', 'SIXTEEN',
    'SEVENTEEN', 'EIGHTEEN', 'NINETEEN'
  ];

  const tens = [
    '', '', 'TWENTY', 'THIRTY', 'FORTY', 'FIFTY', 'SIXTY', 'SEVENTY', 'EIGHTY', 'NINETY'
  ];

  const convertHundreds = (n: number): string => {
    let result = '';
    if (n >= 100) {
      result += ones[Math.floor(n / 100)] + ' HUNDRED';
      n %= 100;
      if (n > 0) result += ' ';
    }
    if (n >= 20) {
      result += tens[Math.floor(n / 10)];
      if (n % 10 > 0) result += ' ' + ones[n % 10];
    } else if (n > 0) {
      result += ones[n];
    }
    return result;
  };

  if (num === 0) return 'ZERO ONLY';

  let wholePart = Math.floor(num);
  const decimalPart = Math.round((num - wholePart) * 100);

  let result = '';

  // Handle crores
  if (wholePart >= 10000000) {
    const crores = Math.floor(wholePart / 10000000);
    result += convertHundreds(crores) + ' CRORE';
    wholePart %= 10000000;
    if (wholePart > 0) result += ' ';
  }

  // Handle lakhs
  if (wholePart >= 100000) {
    const lakhs = Math.floor(wholePart / 100000);
    result += convertHundreds(lakhs) + ' LAKH';
    wholePart %= 100000;
    if (wholePart > 0) result += ' ';
  }

  // Handle thousands
  if (wholePart >= 1000) {
    const thousands = Math.floor(wholePart / 1000);
    result += convertHundreds(thousands) + ' THOUSAND';
    wholePart %= 1000;
    if (wholePart > 0) result += ' ';
  }

  // Handle hundreds
  if (wholePart > 0) {
    result += convertHundreds(wholePart);
  }

  // Handle decimal part (paise)
  if (decimalPart > 0) {
    result += ' AND ' + convertHundreds(decimalPart) + ' PAISE';
  }

  return result.trim().toUpperCase() + ' ONLY';
};

