// FILE: src/pages/TemplateEditor.tsx
/// ANCHOR: TemplateEditor
import { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import AppLayout from '../components/layout/AppLayout';
import { invoke } from '../api/ipcClient';

const TemplateEditor = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const templateName = searchParams.get('name') || 'default-a4-bill.html';
  
  const [html, setHtml] = useState('');
  const [previewHtml, setPreviewHtml] = useState('');
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [showWarning, setShowWarning] = useState(false);
  const [templates, setTemplates] = useState<Array<{ name: string }>>([]);

  const isDefaultTemplate = templateName === 'default-a4-bill.html';

  useEffect(() => {
    loadTemplate();
    loadTemplates();
  }, [templateName]);

  const loadTemplates = async () => {
    try {
      const templateList = await invoke('templates:list', undefined);
      setTemplates(templateList);
    } catch (err) {
      console.error('Error loading templates:', err);
    }
  };

  const loadTemplate = async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await invoke('templates:load', { name: templateName });
      setHtml(result.html);
      setPreviewHtml(result.html);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load template');
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async () => {
    if (isDefaultTemplate && !showWarning) {
      const confirmed = window.confirm(
        'You are about to edit the default template. A backup will be created automatically. Continue?'
      );
      if (!confirmed) return;
      setShowWarning(true);
      return;
    }

    try {
      setSaving(true);
      setError(null);
      const result = await invoke('templates:save', { name: templateName, html });
      if (result.success) {
        alert(result.message || 'Template saved successfully');
        setShowWarning(false);
      } else {
        setError(result.message || 'Failed to save template');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to save template');
    } finally {
      setSaving(false);
    }
  };

  const handlePreview = async () => {
    try {
      const result = await invoke('templates:render', { templateName, sample: true });
      setPreviewHtml(result.html);
      if (result.warnings.length > 0) {
        console.warn('Template warnings:', result.warnings);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to preview template');
    }
  };

  const handleTemplateChange = (newName: string) => {
    navigate(`/billing/templates/editor?name=${encodeURIComponent(newName)}`);
  };

  if (loading) {
    return (
      <AppLayout>
        <div className="page-container">
          <div className="loading">
            <div className="loading-spinner"></div>
            <span style={{ marginLeft: 'var(--spacing-md)' }}>Loading template...</span>
          </div>
        </div>
      </AppLayout>
    );
  }

  return (
    <AppLayout>
      <div className="page-container">
        <div className="page-header">
          <h2>Template Editor</h2>
          <p>Edit bill templates with live preview</p>
        </div>

        {isDefaultTemplate && (
          <div className="alert alert-warning">
            <strong> Warning:</strong> You are editing the default template. A backup will be created when you save.
          </div>
        )}

        {error && (
          <div className="alert alert-error">
            <strong>Error:</strong> {error}
          </div>
        )}

        <div className="card" style={{ marginBottom: 'var(--spacing-xl)' }}>
          <div style={{ 
            display: 'flex', 
            gap: 'var(--spacing-md)', 
            alignItems: 'center',
            flexWrap: 'wrap',
            padding: 'var(--spacing-lg)'
          }}>
            <label style={{ fontWeight: 600, fontSize: '0.9375rem' }}>Template:</label>
            <select
              value={templateName}
              onChange={(e) => handleTemplateChange(e.target.value)}
              style={{ minWidth: '200px' }}
            >
              {templates.map(t => (
                <option key={t.name} value={t.name}>{t.name}</option>
              ))}
            </select>
            <button
              onClick={handlePreview}
              className="btn btn-outline"
            >
              Preview with Sample Data
            </button>
            <button
              onClick={handleSave}
              disabled={saving}
              className="btn btn-primary"
            >
              {saving ? 'Saving...' : 'Save Template'}
            </button>
          </div>
        </div>

        <div style={{ 
          display: 'grid', 
          gridTemplateColumns: '1fr 1fr', 
          gap: 'var(--spacing-xl)', 
          height: 'calc(100vh - 350px)',
          minHeight: '500px'
        }}>
          <div className="card" style={{ display: 'flex', flexDirection: 'column', padding: 0 }}>
            <div style={{ padding: 'var(--spacing-lg)', borderBottom: '1px solid var(--color-border)' }}>
              <label style={{ fontWeight: 600, fontSize: '0.9375rem' }}>HTML Editor</label>
            </div>
            <textarea
              value={html}
              onChange={(e) => setHtml(e.target.value)}
              style={{
                flex: 1,
                padding: 'var(--spacing-lg)',
                fontFamily: '"SF Mono", "Monaco", "Cascadia Code", monospace',
                fontSize: '13px',
                border: 'none',
                borderRadius: 0,
                resize: 'none',
                background: 'var(--color-bg-dark)',
                color: '#e2e8f0',
                lineHeight: '1.6'
              }}
              spellCheck={false}
            />
          </div>
          <div className="card" style={{ display: 'flex', flexDirection: 'column', padding: 0 }}>
            <div style={{ padding: 'var(--spacing-lg)', borderBottom: '1px solid var(--color-border)' }}>
              <label style={{ fontWeight: 600, fontSize: '0.9375rem' }}>Live Preview</label>
            </div>
            <iframe
              srcDoc={previewHtml}
              style={{
                flex: 1,
                border: 'none',
                borderRadius: 0,
                background: 'var(--color-bg-primary)'
              }}
              title="Template Preview"
            />
          </div>
        </div>
      </div>
    </AppLayout>
  );
};

export default TemplateEditor;

