// FILE: src/pages/CompanyInfoPage.tsx
/// ANCHOR: CompanyInfoPage
import { FormEvent, useEffect, useState } from 'react';
import AppLayout from '../components/layout/AppLayout';
import { useCompanyInfo } from '../hooks/useCompanyInfo';
import type { SaveCompanyInfoRequest } from '@shared/ipc';

const CompanyInfoPage = () => {
  const { companyInfo, loading, error, save } = useCompanyInfo();
  const [form, setForm] = useState<SaveCompanyInfoRequest & { state?: string; stateCode?: string }>({
    companyName: '',
    address: '',
    phone: '',
    email: '',
    gstNumber: '',
    dlNumber1: '',
    dlNumber2: '',
    fssaiNumber: '',
    cinPan: '',
    logoPath: '',
    signPath: '',
    state: '',
    stateCode: ''
  });
  const [saving, setSaving] = useState(false);
  const [saveSuccess, setSaveSuccess] = useState(false);

  useEffect(() => {
    if (companyInfo) {
      setForm({
        companyName: companyInfo.companyName || '',
        address: companyInfo.address || '',
        phone: companyInfo.phone || '',
        email: companyInfo.email || '',
        gstNumber: companyInfo.gstNumber || '',
        dlNumber1: companyInfo.dlNumber1 || '',
        dlNumber2: companyInfo.dlNumber2 || '',
        fssaiNumber: companyInfo.fssaiNumber || '',
        cinPan: companyInfo.cinPan || '',
        logoPath: companyInfo.logoPath || '',
        signPath: companyInfo.signPath || '',
        state: (companyInfo as any).state || '',
        stateCode: (companyInfo as any).stateCode || ''
      });
    }
  }, [companyInfo]);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!form.companyName.trim()) {
      alert('Company name is required');
      return;
    }

    setSaving(true);
    setSaveSuccess(false);
    try {
      await save(form);
      setSaveSuccess(true);
      setTimeout(() => setSaveSuccess(false), 3000);
    } catch (err) {
      alert(err instanceof Error ? err.message : 'Failed to save company info');
    } finally {
      setSaving(false);
    }
  };

  const handleLogoSelect = () => {
    // TODO: Implement file picker for logo
    alert('Logo upload feature coming soon');
  };

  const handleSignSelect = () => {
    // TODO: Implement file picker for signature
    alert('Signature upload feature coming soon');
  };

  return (
    <AppLayout>
      <div className="page-container">
        <div className="page-header">
          <h2>Company Information</h2>
          <p>Manage your company details for bills and invoices</p>
        </div>

        {loading && (
          <div className="loading">
            <div className="loading-spinner"></div>
            <span style={{ marginLeft: 'var(--spacing-md)' }}>Loading company information...</span>
          </div>
        )}

        {error && (
          <div className="alert alert-error">
            <strong>Error:</strong> {error}
          </div>
        )}

        {saveSuccess && (
          <div className="alert alert-success">
            Company information saved successfully!
          </div>
        )}

        <section className="panel" style={{ maxWidth: '900px' }}>
          <header className="panel__header">
            <div>
              <h3>Company Profile</h3>
              <p>This information will be auto-filled in all bills and invoices</p>
            </div>
          </header>
          <div className="panel__body">
            <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: 'var(--spacing-xl)' }}>
              <div>
                <label>
                  Company Name *
                </label>
                <input
                  type="text"
                  placeholder="Your Company Name"
                  value={form.companyName}
                  onChange={(e) => setForm({ ...form, companyName: e.target.value })}
                  required
                />
              </div>

              <div>
                <label>
                  Address
                </label>
                <textarea
                  placeholder="Company Address"
                  value={form.address}
                  onChange={(e) => setForm({ ...form, address: e.target.value })}
                  rows={3}
                />
              </div>

              <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: 'var(--spacing-lg)' }}>
                <div>
                  <label>
                    Phone
                  </label>
                  <input
                    type="text"
                    placeholder="Phone Number"
                    value={form.phone}
                    onChange={(e) => setForm({ ...form, phone: e.target.value })}
                  />
                </div>

                <div>
                  <label>
                    Email
                  </label>
                  <input
                    type="email"
                    placeholder="Email Address"
                    value={form.email}
                    onChange={(e) => setForm({ ...form, email: e.target.value })}
                  />
                </div>
              </div>

              <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: 'var(--spacing-lg)' }}>
                <div>
                  <label>
                    GST Number
                  </label>
                  <input
                    type="text"
                    placeholder="GSTIN"
                    value={form.gstNumber}
                    onChange={(e) => setForm({ ...form, gstNumber: e.target.value })}
                  />
                </div>

                <div>
                  <label>
                    FSSAI Number
                  </label>
                  <input
                    type="text"
                    placeholder="FSSAI Number"
                    value={form.fssaiNumber}
                    onChange={(e) => setForm({ ...form, fssaiNumber: e.target.value })}
                  />
                </div>
              </div>

              <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: 'var(--spacing-lg)' }}>
                <div>
                  <label>
                    Drug License No. 1
                  </label>
                  <input
                    type="text"
                    placeholder="DL Number 1"
                    value={form.dlNumber1}
                    onChange={(e) => setForm({ ...form, dlNumber1: e.target.value })}
                  />
                </div>

                <div>
                  <label>
                    Drug License No. 2
                  </label>
                  <input
                    type="text"
                    placeholder="DL Number 2"
                    value={form.dlNumber2}
                    onChange={(e) => setForm({ ...form, dlNumber2: e.target.value })}
                  />
                </div>
              </div>

              <div>
                <label>
                  CIN / PAN (Optional)
                </label>
                <input
                  type="text"
                  placeholder="CIN or PAN Number"
                  value={form.cinPan}
                  onChange={(e) => setForm({ ...form, cinPan: e.target.value })}
                />
              </div>

              <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: 'var(--spacing-lg)' }}>
                <div>
                  <label>
                    State
                  </label>
                  <input
                    type="text"
                    placeholder="State Name"
                    value={form.state || ''}
                    onChange={(e) => setForm({ ...form, state: e.target.value })}
                  />
                </div>

                <div>
                  <label>
                    State Code
                  </label>
                  <input
                    type="text"
                    placeholder="State Code (e.g., 29)"
                    value={form.stateCode || ''}
                    onChange={(e) => setForm({ ...form, stateCode: e.target.value })}
                  />
                </div>
              </div>

              <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: 'var(--spacing-lg)' }}>
                <div>
                  <label>
                    Logo (Optional)
                  </label>
                  <div style={{ display: 'flex', gap: 'var(--spacing-md)' }}>
                    <input
                      type="text"
                      placeholder="Logo file path"
                      value={form.logoPath}
                      onChange={(e) => setForm({ ...form, logoPath: e.target.value })}
                      style={{ flex: 1 }}
                    />
                    <button
                      type="button"
                      onClick={handleLogoSelect}
                      className="btn btn-outline"
                    >
                      Browse
                    </button>
                  </div>
                </div>

                <div>
                  <label>
                    Digital Signature (Optional)
                  </label>
                  <div style={{ display: 'flex', gap: 'var(--spacing-md)' }}>
                    <input
                      type="text"
                      placeholder="Signature file path"
                      value={form.signPath}
                      onChange={(e) => setForm({ ...form, signPath: e.target.value })}
                      style={{ flex: 1 }}
                    />
                    <button
                      type="button"
                      onClick={handleSignSelect}
                      className="btn btn-outline"
                    >
                      Browse
                    </button>
                  </div>
                </div>
              </div>

              <div style={{ marginTop: 'var(--spacing-lg)', display: 'flex', gap: 'var(--spacing-md)', justifyContent: 'flex-end' }}>
                <button
                  type="submit"
                  disabled={saving || loading}
                  className="btn btn-primary"
                >
                  {saving ? 'Saving...' : 'Save Company Info'}
                </button>
              </div>
            </form>
          </div>
        </section>
      </div>
    </AppLayout>
  );
};

export default CompanyInfoPage;

