// FILE: src/pages/StockPage.tsx
/// ANCHOR: StockPage
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import AppLayout from '../components/layout/AppLayout';
import StockKPICards from '../components/dashboard/StockKPICards';
import StockSnapshotEnhanced from '../components/dashboard/StockSnapshotEnhanced';
import StockAlertsEnhanced from '../components/dashboard/StockAlertsEnhanced';
import StockMovementEnhanced from '../components/dashboard/StockMovementEnhanced';
import ExpiryManagementEnhanced from '../components/dashboard/ExpiryManagementEnhanced';

const StockPage = () => {
  const navigate = useNavigate();
  const [globalSearch, setGlobalSearch] = useState('');

  const quickActions = [
    { 
      label: ' Add Stock', 
      action: () => navigate('/products/create'),
      tooltip: 'Create a new product'
    },
    { 
      label: ' Add Purchase', 
      action: () => navigate('/billing/new?type=PURCHASE'),
      tooltip: 'Create a new purchase invoice'
    },
    { 
      label: ' View Products', 
      action: () => navigate('/products'),
      tooltip: 'View all products'
    },
    { 
      label: ' View Purchases', 
      action: () => navigate('/purchases'),
      tooltip: 'View purchase invoices'
    }
  ];

  return (
    <AppLayout>
      <div className="page-container">
        <div className="page-header">
          <h2>Stock Management Dashboard</h2>
          <p>Complete inventory tracking and management</p>
        </div>

        <div className="search-bar">
          <div style={{ flex: 1, minWidth: '250px' }}>
            <label
              style={{
                display: 'block',
                fontSize: '0.8125rem',
                fontWeight: 600,
                marginBottom: 'var(--spacing-sm)',
                color: 'var(--color-text-secondary)'
              }}
            >
               Global Search
            </label>
            <input
              type="text"
              placeholder="Search products across all sections..."
              value={globalSearch}
              onChange={(e) => setGlobalSearch(e.target.value)}
            />
          </div>

          {/* Quick Actions */}
          <div
            style={{
              display: 'flex',
              gap: 'var(--spacing-sm)',
              flexWrap: 'wrap',
              alignItems: 'flex-end'
            }}
          >
            {quickActions.map((action, idx) => (
              <button
                key={idx}
                onClick={action.action}
                title={action.tooltip}
              >
                {action.label}
              </button>
            ))}
          </div>
        </div>

        {/* KPI Cards */}
        <StockKPICards />

        {/* Main Content Grid */}
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            gap: 'var(--spacing-xl)'
          }}
        >
          <StockSnapshotEnhanced globalSearch={globalSearch} />
          <StockAlertsEnhanced globalSearch={globalSearch} />
          <StockMovementEnhanced globalSearch={globalSearch} />
          <ExpiryManagementEnhanced globalSearch={globalSearch} />
        </div>
      </div>
    </AppLayout>
  );
};

export default StockPage;
