// FILE: src/stores/authStore.ts
/// ANCHOR: AuthStore
import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import type { LoginRequest, LoginResponse } from '@shared/ipc';
import { invoke } from '../api/ipcClient';

interface AuthState {
  user: LoginResponse | null;
  status: 'idle' | 'loading' | 'error';
  error?: string;
  login: (payload: LoginRequest) => Promise<void>;
  logout: () => void;
  resetError: () => void;
  checkSession: () => Promise<void>;
}

export const useAuthStore = create<AuthState>()(
  persist(
    (set, get) => ({
      user: null,
      status: 'idle',
      error: undefined,
      login: async (payload) => {
        set({ status: 'loading', error: undefined });
        try {
          const user = await invoke('ipc.auth.login', payload);
          set({ user, status: 'idle', error: undefined });
        } catch (error) {
          set({
            status: 'error',
            error: error instanceof Error ? error.message : 'Login failed',
            user: null
          });
          throw error;
        }
      },
      logout: () => {
        set({ user: null, status: 'idle', error: undefined });
      },
      resetError: () => set({ error: undefined, status: 'idle' }),
      checkSession: async () => {
        const { user } = get();
        if (!user) return;
        
        try {
          const valid = await invoke('ipc.security.checkSession', { userId: parseInt(Buffer.from(user.token, 'base64').toString().split(':')[0]) });
          if (!valid) {
            set({ user: null, status: 'idle', error: undefined });
          }
        } catch {
          set({ user: null, status: 'idle', error: undefined });
        }
      }
    }),
    {
      name: 'auth-storage',
      partialize: (state) => ({ user: state.user })
    }
  )
);

