// FILE: src/pages/Login.tsx
/// ANCHOR: LoginPage
import { FormEvent, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuthStore } from '../stores/authStore';

const LoginPage = () => {
  const { login, status, error, user, resetError } = useAuthStore();
  const navigate = useNavigate();
  const [username, setUsername] = useState('admin@local');
  const [password, setPassword] = useState('');

  useEffect(() => {
    if (user) {
      navigate('/dashboard', { replace: true });
    } else {
      // Reset form and error state when user logs out
      setPassword('');
      resetError();
    }
  }, [user, navigate, resetError]);

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    try {
      await login({ username, password });
      // Password will be cleared by useEffect when user is set
    } catch {
      // Error is handled by authStore
      setPassword('');
    }
  };

  return (
    <div className="login-page">
      <div className="login-card">
        <h1>Mediclone ERP</h1>
        <p className="subtitle">Offline Wholesale Pharma ERP</p>
        <form onSubmit={handleSubmit}>
          <label>
            Email
            <input
              value={username}
              onChange={(event) => setUsername(event.target.value)}
              type="email"
              placeholder="admin@local"
              required
            />
          </label>
          <label>
            Password
            <input
              value={password}
              onChange={(event) => setPassword(event.target.value)}
              type="password"
              placeholder=""
              required
            />
          </label>
          {error ? <p className="error">{error}</p> : null}
          <button type="submit" disabled={status === 'loading'}>
            {status === 'loading' ? 'Verifying...' : 'Sign in'}
          </button>
        </form>
      </div>
    </div>
  );
};

export default LoginPage;

