// FILE: src/pages/BillViewPage.tsx
/// ANCHOR: BillViewPage
import { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import AppLayout from '../components/layout/AppLayout';
import type { BillDTO } from '@shared/ipc';
import { invoke } from '../api/ipcClient';

const BillViewPage = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [bill, setBill] = useState<BillDTO | null>(null);
  const [loading, setLoading] = useState(true);
  const [previewHtml, setPreviewHtml] = useState<string>('');
  const [printing, setPrinting] = useState(false);

  useEffect(() => {
    if (!id) {
      navigate('/billing/new');
      return;
    }

    loadBill();
  }, [id]);

  const loadBill = async () => {
    try {
      const billData = await invoke('ipc.bill.get', { id: Number(id) });
      setBill(billData);
    } catch (error) {
      alert('Failed to load bill');
      navigate('/billing/new');
    } finally {
      setLoading(false);
    }
  };

  const handlePrint = async (toPrinter: boolean = false) => {
    if (!bill) return;

    setPrinting(true);
    try {
      if (toPrinter) {
        await invoke('ipc.bill.printToPrinter', { id: bill.id, silent: false });
        alert('Print job sent to printer');
      } else {
        const result = await invoke('ipc.bill.print', { id: bill.id });
        alert(`PDF saved to: ${result.filePath}`);
      }
    } catch (error) {
      alert(error instanceof Error ? error.message : 'Failed to print');
    } finally {
      setPrinting(false);
    }
  };

  const handlePreview = async () => {
    if (!bill) return;

    try {
      const result = await invoke('ipc.bill.preview', { id: bill.id });
      setPreviewHtml(result.html);
    } catch (error) {
      alert(error instanceof Error ? error.message : 'Failed to generate preview');
    }
  };

  const handleFinalize = async () => {
    if (!bill || bill.status !== 'DRAFT') return;

    if (!confirm('Finalize this bill? Stock will be updated and the bill will be locked.')) {
      return;
    }

    try {
      await invoke('ipc.bill.finalize', { id: bill.id });
      alert('Bill finalized successfully!');
      await loadBill();
    } catch (error) {
      alert(error instanceof Error ? error.message : 'Failed to finalize bill');
    }
  };

  if (loading) {
    return (
      <AppLayout>
        <div className="page-container">
          <div>Loading...</div>
        </div>
      </AppLayout>
    );
  }

  if (!bill) {
    return (
      <AppLayout>
        <div className="page-container">
          <div>Bill not found</div>
        </div>
      </AppLayout>
    );
  }

  const isEditable = bill.status === 'DRAFT';
  const isFinalized = bill.status === 'FINALIZED';
  const isCancelled = bill.status === 'CANCELLED';

  return (
    <AppLayout>
      <div className="page-container">
        <div className="page-header" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <div>
            <h2>Bill #{bill.billNumber}</h2>
            <p>{bill.billType} Bill - {bill.status}</p>
          </div>
          <div style={{ display: 'flex', gap: '0.5rem' }}>
            <button onClick={handlePreview}>Preview</button>
            <button onClick={() => handlePrint(false)} disabled={printing}>
              {printing ? 'Generating PDF...' : 'Export PDF'}
            </button>
            {isFinalized && (
              <button onClick={() => handlePrint(true)} disabled={printing}>
                {printing ? 'Printing...' : 'Print'}
              </button>
            )}
            {isEditable && (
              <button onClick={() => navigate(`/billing/edit/${bill.id}`)}>
                Edit
              </button>
            )}
            {isEditable && (
              <button onClick={handleFinalize} style={{ backgroundColor: '#059669', color: 'white' }}>
                Finalize
              </button>
            )}
            <button onClick={() => navigate('/billing/new')}>Back</button>
          </div>
        </div>

        <div className="dashboard-grid">
          <section className="panel">
            <header className="panel__header">
              <h3>Bill Details</h3>
            </header>
            <div className="panel__body">
              <div className="form-grid form-grid--2cols">
                <div>
                  <strong>Bill Number:</strong> {bill.billNumber}
                </div>
                <div>
                  <strong>Date:</strong> {new Date(bill.billDate).toLocaleDateString()}
                </div>
                <div>
                  <strong>Type:</strong> {bill.billType}
                </div>
                <div>
                  <strong>Status:</strong> {bill.status}
                </div>
                {bill.customerName && (
                  <div>
                    <strong>Customer:</strong> {bill.customerName}
                  </div>
                )}
                {bill.supplierName && (
                  <div>
                    <strong>Supplier:</strong> {bill.supplierName}
                  </div>
                )}
                {bill.customerGstin && (
                  <div>
                    <strong>GSTIN:</strong> {bill.customerGstin}
                  </div>
                )}
                {bill.remarks && (
                  <div className="form-field--full">
                    <strong>Remarks:</strong> {bill.remarks}
                  </div>
                )}
              </div>
            </div>
          </section>

          <section className="panel">
            <header className="panel__header">
              <h3>Line Items</h3>
            </header>
            <div className="panel__body">
              <table className="table">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Product</th>
                    <th>Batch</th>
                    <th>Qty</th>
                    <th>Free</th>
                    <th>Rate</th>
                    <th>Disc %</th>
                    <th>GST %</th>
                    <th>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {bill.lines.map((line, index) => (
                    <tr key={line.id}>
                      <td>{line.lineNumber}</td>
                      <td>{line.productName} {line.packSize ? `(${line.packSize})` : ''}</td>
                      <td>{line.batchNumber} {line.expiryDate ? `(Exp: ${new Date(line.expiryDate).toLocaleDateString()})` : ''}</td>
                      <td>{line.quantity}</td>
                      <td>{line.freeQuantity || 0}</td>
                      <td>{line.rate.toFixed(2)}</td>
                      <td>{line.discountPercent.toFixed(2)}%</td>
                      <td>{line.taxPercent.toFixed(2)}%</td>
                      <td style={{ textAlign: 'right' }}>{line.lineTotal.toFixed(2)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </section>

          <section className="panel">
            <header className="panel__header">
              <h3>Totals</h3>
            </header>
            <div className="panel__body">
              <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '0.5rem' }}>
                <div><strong>Subtotal:</strong></div>
                <div style={{ textAlign: 'right' }}>{bill.subtotal.toFixed(2)}</div>
                <div><strong>Total Discount:</strong></div>
                <div style={{ textAlign: 'right' }}>{bill.totalDiscount.toFixed(2)}</div>
                <div><strong>CGST:</strong></div>
                <div style={{ textAlign: 'right' }}>{bill.cgstTotal.toFixed(2)}</div>
                <div><strong>SGST:</strong></div>
                <div style={{ textAlign: 'right' }}>{bill.sgstTotal.toFixed(2)}</div>
                <div><strong>IGST:</strong></div>
                <div style={{ textAlign: 'right' }}>{bill.igstTotal.toFixed(2)}</div>
                <div><strong>Round Off:</strong></div>
                <div style={{ textAlign: 'right' }}>{bill.roundOff.toFixed(2)}</div>
                <div style={{ fontSize: '1.2em', fontWeight: 700 }}><strong>Total Amount:</strong></div>
                <div style={{ fontSize: '1.2em', fontWeight: 700, textAlign: 'right' }}>{bill.totalAmount.toFixed(2)}</div>
                {bill.paidAmount > 0 && (
                  <>
                    <div><strong>Paid:</strong></div>
                    <div style={{ textAlign: 'right' }}>{bill.paidAmount.toFixed(2)}</div>
                    <div><strong>Balance:</strong></div>
                    <div style={{ textAlign: 'right' }}>{(bill.totalAmount - bill.paidAmount).toFixed(2)}</div>
                  </>
                )}
              </div>
            </div>
          </section>
        </div>

        {previewHtml && (
          <div style={{ marginTop: '2rem', border: '1px solid #ddd', padding: '1rem' }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '1rem' }}>
              <h3>Print Preview</h3>
              <button onClick={() => setPreviewHtml('')}>Close</button>
            </div>
            <iframe
              srcDoc={previewHtml}
              style={{ width: '100%', height: '800px', border: '1px solid #ccc' }}
              title="Bill Preview"
            />
          </div>
        )}
      </div>
    </AppLayout>
  );
};

export default BillViewPage;

