/// ANCHOR: SalesPage
import AppLayout from '../components/layout/AppLayout';
import SalesEntryPanel from '../components/dashboard/SalesEntryPanel';
import CustomerPanel from '../components/dashboard/CustomerPanel';
import CreditNotePanel from '../components/dashboard/CreditNotePanel';
import SchemePanel from '../components/dashboard/SchemePanel';

const SalesPage = () => (
  <AppLayout>
    <div className="page-container">
      <div className="page-header">
        <div>
          <h2>Sales</h2>
          <p>Manage sales invoices, customers, credit notes, and schemes</p>
        </div>
      </div>
      <div className="sales-layout">
        <div className="sales-layout__main">
          <SalesEntryPanel />
        </div>
        <div className="sales-layout__side">
          <CustomerPanel />
          <CreditNotePanel />
          <SchemePanel />
        </div>
      </div>
    </div>
  </AppLayout>
);

export default SalesPage;

